.link {
    text-decoration: none;
    color: inherit;
}

.list {
    padding: 0;
    margin: 0;

    list-style: none;

}

li{
    list-style: none;
}

h1,
h2,
h3,
p,
ul {
    margin-top: 0;
    margin-bottom: 0;
}

*::before,
*::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
    list-style: none;
}

img {
    display: block;
    width: 100%;
    height: auto;
}

button {
    cursor: pointer;
}